export default {
  layout: "Dum-content",
  mounted() {
    this.getTution();
    this.getProgram();
  },
  data() {
    return {
      tutions: [],
      tution: {
        name_of_program: "",
        duration: "",
        type: "",
        total_fee: "",
      },
      errors: {},
      programs:'',
    };
  },
  methods: {
          getProgram() {
      this.$axios
        .$get("/program/show")
        .then((res) => {
          this.programs = res;
        })
        .catch((err) => {
          console.log(err);
        });
    },
    getTution() {
      this.$axios
        .$get("/tution/show")
        .then((res) => {
            console.log(res);
          this.tutions = res;
        })
        .catch((err) => {
          console.log(err);
        });
    },
    tutionEdit(id) {
      $("#tutionUpdate").modal("show");
      this.$axios
        .$get("/tution/edit/" + id)
        .then((res) => {
          this.tution = res;
        })
        .catch((err) => {
          console.log(err);
        });
    },
    tutionUpdate() {
      this.$axios
        .$post("/tution/update/" + this.tution.id, this.tution)
        .then((res) => {
          this.getTution();
          $("#tutionUpdate").modal("hide");
          this.$toaster.success(res.message);
          this.errors = "";
        })
        .catch((err) => {
          console.log(err);
          this.errors = err.response.data.errors;
        });
    },
    deletetution(id) {
      if (confirm("Are you sure to delete this tution?")) {
        this.$axios
          .$get("/tution/delete/" + id)
          .then((res) => {
            this.getTution();
            this.$toaster.error(res.message);
          })
          .catch((err) => {
            console.log(err);
          });
      }
    },
    tutionStatus(id) {
      this.$axios
        .$get("/tution/status/" + id )
        .then((res) => {
          this.getTution();
          this.$toaster.success(res.message);
        })
        .catch((err) => {
          console.log(err);
        });
    },
  },
};
